import React from "react";
import { Grid, Typography, Tooltip } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import { translate, camleCaseString } from "../utils";

const useStyles = makeStyles({
	fieldsContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		background: "#e7eaec",
		borderRadius: 5,
	},
	fieldImage: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		padding: 15,
	},
	fieldLabel: {
		fontWeight: "bold",
	},
	icon: {
		width: 20,
		height: 20,
		backgroundRepeat: "no-repeat",
		backgroundSize: "contain",
		backgroundPosition: "center",
		position: "absolute",
	},
	gridContainer: {
		display: "flex",
		alignItems: "center",
	},
	labelContainer: {
		whiteSpace: "nowrap",
		overflow: "hidden",
		textOverflow: "ellipsis",
	},
});

const getImageURL = (widget) => {
	const type = widget.serverType || widget.type;
	if(type && type.indexOf('datetime') !== -1) {
		return 'datetime.png';
	}
	if(type && type.indexOf('date') !== -1) {
		return 'date.png';
	}
	if(type && type.indexOf('time') !== -1) {
		return 'time.png';
	}
	if(type && type.indexOf('decimal') !== -1) {
		return 'decimal.png';
	}
	if(widget.image) {
		return widget.image;
	}
	return `${type}.png`;
}

export function FieldComponent(props) {
	const classes = useStyles();
	const { attrs, targetName = "name" } = props;
	return (
		<div className={classes.fieldsContainer}>
			<Tooltip
				arrow
				title={translate(camleCaseString(attrs[targetName] || attrs.title))}
				placement="top"
			>
				<Grid container className={classes.gridContainer}>
					<Grid xs={4} item className={classes.fieldImage}>
						<div
							className={classes.icon}
							style={{
								backgroundImage: `url(./images/${getImageURL(attrs)})`,
							}}
						/>
					</Grid>
					<Grid xs={8} item className={classes.labelContainer}>
						<Typography variant="caption" className={classes.fieldLabel}>
							{translate(camleCaseString(attrs[targetName] || attrs.title))}
						</Typography>
					</Grid>
				</Grid>
			</Tooltip>
		</div>
	);
}

export default FieldComponent;
