import React from "react";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { DndProvider } from "react-dnd";
import Backend from "react-dnd-html5-backend";

import SidePanel from "./Panels/SidePanel";
import MainPanel from "./Panels/MainPanel";
import Toolbar from "./Toolbar/ToolbarContainer";
import StudioProvider, { useStore } from "./store/context";
import {fetchLanguages, fetchUserPreferences} from "./Toolbar/api";

const useStyles = makeStyles({
	container: {
		display: "flex",
		height: "100%",
	},
	toolbarWrapper: {
		height: "60px",
	},
	panelContainer: {
		display: "flex",
		height: "calc(100% - 60px)",
	},
	sidePanel: {
		height: "100%",
	},
	mainPanel: {
		height: "100%",
		'.modern-dark &': {
			backgroundColor: "#323232",
		},
	},
});

function AppContent() {
	const classes = useStyles();
	const {state, update} = useStore();
	const { sidePanel = true } = state;

	React.useEffect(() => {
		fetchLanguages().then(result => {
			update(draft => {
				draft.languageList = [...result];
			});
		});
		fetchUserPreferences().then(result => {
			if(result && result.theme) {
				document.body.className = result.theme;
			}
		});
	}, [update]);

	return (
		<Grid container className={classes.container}>
			<Grid item xs={12} className={classes.toolbarWrapper}>
				<Toolbar />
			</Grid>
			<Grid container className={classes.panelContainer}>
				{sidePanel && (
					<Grid item xs={3} className={classes.sidePanel}>
						<SidePanel />
					</Grid>
				)}
				<Grid item xs={sidePanel ? 9 : 12} className={classes.mainPanel}>
					<MainPanel />
				</Grid>
			</Grid>
		</Grid>
	);
}

export default function App() {
	return (
		<StudioProvider>
			<DndProvider backend={Backend}>
				<AppContent />
			</DndProvider>
		</StudioProvider>
	);
}
