import React, { useState, useEffect } from "react";
import { TextField } from "@material-ui/core";
import AutoComplete from "@material-ui/lab/Autocomplete";
import { makeStyles } from "@material-ui/core/styles";

import AxelorService from "./../services/axelor.rest";
import { useDebounceEffect } from "./../common.func";
import SearchView from '../components/SearchView';

const useStyles = makeStyles({
	autoComplete: {
		"& > div > label": {
			color: "#e7eaec !important",
			fontSize: 14,
		},
	},
	autoCompleteRoot: {
		width: 200,
		marginLeft: 10,
		color: "#e7eaec",
	},
	ddinput: {
		color: "#e7eaec",
		fontSize: 14,
	},
	inputView: {
		color: "#fff",
		"& > *": {
			color: "#fff",
		},
	},
});

function Select({
	value,
	label,
	model,
	searchFilter,
	onChange,
	options,
	filterData,
	limit = 10,
	onSearch,
	modelType,
	...props
}) {
	const classes = useStyles();
	const [searchText, setSearchText] = useState("");
	const [loading, setLoading] = useState(false);
	const [list, setList] = useState([...(options ? options : [])]);
	const [searchMore, setSearchMore] = useState(false);
	const [offset, setOffset] = useState(0);
	const [total, setTotal] = useState(0);

	const search = React.useCallback(
		async (searchText = "") => {
			if(onSearch) {
				setLoading(true);
				const {models, total} = await onSearch({limit, searchText, searchFilter, filterData, modelType, offset})
				!searchMore && total > limit && models.push({ name: "Show more...", id: 'studio_show_More_View' })
				setList([...models]);
				setLoading(false);
				setTotal(total);
			} else {
				const service = new AxelorService({ model });
				setLoading(true);
				const _data = searchFilter(searchText) || {};
				const criteria = [];
				if (searchText) {
					criteria.push({
						fieldName: "name",
						operator: "like",
						value: searchText,
					});
				}
				const data = {
					...(_data._domain && { _domain: _data._domain }),
					...(_data._domainContext && { _domainContext: _data._domainContext }),
					criteria: [...criteria, ...(_data.criteria || [])],
					operator: "and",
				};
				const fields = _data.fields || [];
				service
					.search({ fields, data, limit, offset: offset })
					.then((response = {}) => {
						const { data = [], total } = response;
						setLoading(false);
						const list = filterData ? filterData([...data]) : [...data];
						!searchMore && total > limit && list.push({ name: "Show more...", id: 'studio_show_More_View' })
						setTotal(total);
						setList(list);
					});
			}
		},
		[searchFilter, model, filterData, searchMore, limit, onSearch, modelType, offset]
	);

	const handleNext = React.useCallback(() => {
		const newOffset = offset + limit;
		if(newOffset <= total) {
			setOffset(newOffset);
		}
	}, [limit, total, offset]);

	const handlePrevious = React.useCallback(() => {
		setOffset(offset => {
			const newOffset = offset-limit;
			return newOffset > 0 ? newOffset : 0;
		})
	}, [limit]);

	const debounceHandler = React.useCallback(() => {
		!options && search(searchText);
	}, [searchText, search, options]);

	const handleChange = React.useCallback(
		(e, value) => {
			if(value?.id === 'studio_show_More_View') {
				setSearchMore(true);
			} else {
				onChange(value);
			}
		},
		[onChange]
	);

	const handleTextChange = React.useCallback((value) => {
		setSearchText(value);
		setOffset(0);
	}, []);

	const handleClose = React.useCallback(() => {
		setSearchMore(false);
		setOffset(0);
	}, []);

	useDebounceEffect(debounceHandler, 500);

	useEffect(() => {
		!options && search();
	}, [search, options, model]);

	useEffect(() => {
		if (!value) {
			setSearchText("");
		}
	}, [value]);
	return (
		<React.Fragment>

			<AutoComplete
				clearOnEscape
				autoComplete
				classes={{
					root: classes.autoCompleteRoot,
					inputFocused: classes.input,
					clearIndicator: classes.input,
					popupIndicator: classes.input,
					input: classes.inputView,
					endAdornment: classes.inputView,
				}}
				className={classes.autoComplete}
				onInputChange={(e, value) => setSearchText(value)}
				renderInput={(params) => {
					params.inputProps.value = searchText;
					return <TextField {...params} label={label} fullWidth />;
				}}
				options={[...list]}
				loading={loading}
				getOptionLabel={(option) =>
					typeof option === "string"
						? option
						: props.getOptionLabel
						? props.getOptionLabel(option)
						: option.name
				}
				onChange={handleChange}
				filterOptions={(options) => options}
				value={value || null}
				getOptionSelected={(option, value) => option.name === value.name}
				{...props}
			/>
			<SearchView
				searchText={searchText}
				list={list}
				model={model}
				total={total}
				offset={offset}
				open={searchMore}
				onNext={handleNext}
				onPrevious={handlePrevious}
				handleClose={handleClose}
				setSearchText={handleTextChange}
				onChange={onChange}
				title={label}
				getOptionLabel={props.getOptionLabel}
			/>
		</React.Fragment>
	);
}

export default React.memo(Select);
