import React, { useState, useEffect, useCallback } from "react";
import AutoComplete, {
	createFilterOptions,
} from "@material-ui/lab/Autocomplete";
import { TextField, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import AxelorService from "../services/axelor.rest";
import { camleCaseString } from "../utils";
import { useDebounceEffect } from "./../common.func";
import SearchView from './SearchView';
const filter = createFilterOptions();
const useStyles = makeStyles({
	autoComplete: {
		backgroundColor: "#2f4050",
		marginBottom: 5,
		"& > div > label": {
			color: "#e7eaec !important",
			fontSize: 14,
		},
		'.modern-dark &': {
			backgroundColor: "#323232",
		}
	},
	input: {
		color: "#e7eaec",
		fontSize: 14,
	},
	root: {
		color: "#e7eaec",
	},
	error: {
		color: 'red',
		marginBottom: 5,
		fontSize: 14,
	}
});

export default function SelectComponent(_props) {
	const {
		name,
		ref,
		title,
		valueField = "id",
		type,
		multiple = false,
		commaSeparated,
		displayField = "name",
		limit = 10,
		data: preData = [],
		isDisabled,
		needsTargetModel,
		showMore = false,
		canAddNew = true,
		getOptionLabel,
		fetchFieldList = [],
		shouldFetchInStart = false,
	} = _props.field;
	const { props, error } = _props;
	const {
		propertyList,
		setPropertyList,
		onChange,
		editWidgetType,
		modelType,
	} = props;
	let value = propertyList[name] || "";
	if (commaSeparated && type === "select") {
		const values = value ? value.split(",") : [];
		value = values.map((val) => {
			return { [valueField]: val };
		});
	}
	const [data, setData] = useState([...preData]);
	const [searchText, setsearchText] = useState("");
	const isMounted = React.useRef(true);
	const [moreDialog, setMoreDialog] = useState(false);
	const [offset, setOffset] = useState(0);
	const [total, setTotal] = useState(0);
	const [initialFetch, setInitialFetch] = useState(false);
	const { targetModel } = propertyList;
	const classes = useStyles();
	const disabled = isDisabled
		? isDisabled(propertyList, editWidgetType, modelType)
		: false;

	const handleInputChange = useCallback(
		(e, value, reason) => {
			if (reason !== "reset") {
				if (type === "objectSelection" && typeof value === "object") {
					setsearchText(value[displayField]);
				} else {
					setsearchText(value);
				}
			}
		},
		[type, displayField]
	);

	const getSelectValue = useCallback(
		(value = []) => {
			if (commaSeparated) {
				return value ? value.map((e) => e.name).join(",") : "";
			}
			return ref ? value[valueField] : value;
		},
		[valueField, commaSeparated, ref]
	);

	const fetchOptions = useCallback(
		(searchText = "", offset = 0) => {
			const criteria = [];
			if (searchText) {
				criteria.push({
					fieldName: "name",
					operator: "like",
					value: searchText,
				});
			}
			if ((name === "gridView" || name === "formView") && needsTargetModel.includes(props.type)) {
				if (!targetModel) return;
			}
			if (name === "gridView") {
				needsTargetModel.includes(props.type) && criteria.push({
					fieldName: "model",
					operator: "=",
					value: targetModel,
				});
				criteria.push({ fieldName: "type", operator: "=", value: "grid" });
			}
			if (name === "formView") {
				needsTargetModel.includes(props.type) && criteria.push({
					fieldName: "model",
					operator: "=",
					value: targetModel,
				});
				criteria.push({ fieldName: "type", operator: "=", value: "form" });
			}
			const data = {
				criteria,
				operator: "and",
			};
			const fields = ["name", "fullName", valueField, displayField, ...fetchFieldList].filter(
				(e) => e
			);
			if(shouldFetchInStart && initialFetch) {
				// setData(data);
			} else {
				return new AxelorService({ model: ref })
					.search({ fields, data, limit, offset })
					.then(({ data, total }) => {
						setInitialFetch(true);
						if (data && data.length) {
							if (isMounted.current) {
								!moreDialog && total > limit && data.push({ name: "Show more...", id: 'studio_show_More_View' })
								setTotal(total);
								setData(data);
							}
						}
					});
			}
		},
		[name, ref, targetModel, valueField, displayField, limit, needsTargetModel, props.type, moreDialog, initialFetch, shouldFetchInStart]
	);

	const optionDebounceHandler = React.useCallback(() => {
		ref && fetchOptions(searchText);
	}, [fetchOptions, searchText, ref]);

	const getValue = (value) => {
		return typeof value === "string" && ref ? { [valueField]: value } : value;
	};

	const getInputAsOption = React.useCallback((value) => {
		if (value && typeof value === "string" && ref) {
			value = { [valueField]: value };
		}
		return value;
	}, [ref, valueField]);

	const handleSearch = React.useCallback((value) => {
		setsearchText(value);
		setOffset(0);
	}, []);

	const handleNext = React.useCallback(() => {
		const newOffset = offset + limit;
		if(newOffset <= total) {
			setOffset(newOffset);
			fetchOptions(searchText, newOffset)
		}
	}, [limit, total, offset, fetchOptions, searchText]);

	const handlePrevious = React.useCallback(() => {
		let newOffset = offset-limit;
		newOffset = newOffset > 0 ? newOffset : 0;
		setOffset(newOffset)
		fetchOptions(searchText, newOffset)
	}, [offset, limit, searchText, fetchOptions]);

	const handleClose = React.useCallback(() => {
		setMoreDialog(false);
		setOffset(0);
	}, []);

	const handleChange = React.useCallback((e, _value) => {
		if(_value?.id === 'studio_show_More_View') {
			setMoreDialog(true);
			return;
		}
		if (multiple) {
			if(e.fromDialog) {
				_value = [...value, _value];
			} else {
				const hasMoreView = _value.findIndex(v => v?.id === 'studio_show_More_View') !== -1;
				if(hasMoreView) {
					setMoreDialog(true);
				}
			}
			_value = _value.map((val) => {
				if(val?.id !== 'studio_show_More_View') {
					return getInputAsOption(val)
				}
				return undefined;
			}).filter(e => e);
		} else {
			if (_value && _value.inputValue && _value.inputValue.length && ref) {
				_value[valueField] = _value.inputValue;
			}
		}
		setsearchText("");
		setPropertyList({
			...propertyList,
			[name]:
				type === "objectSelection"
					? _value
					: _value && getSelectValue(_value),
		});
		onChange({
			...propertyList,
			[name]:
				type === "objectSelection"
					? _value
					: _value && getSelectValue(_value),
		});
	}, [getSelectValue, multiple, name, onChange, setPropertyList, propertyList, value, type, ref, getInputAsOption,valueField]);


	useDebounceEffect(optionDebounceHandler, 500);

	useEffect(() => {
		isMounted.current = true;
		fetchOptions();
		return () => {
			isMounted.current = false;
		};
	}, [fetchOptions]);

	useEffect(() => {
		if (!multiple) {
			if (typeof value === "object") {
				setsearchText(value[displayField]);
			} else {
				setsearchText(value);
			}
		}
	}, [multiple, value, displayField, type]);
	return (
		<React.Fragment>
			<AutoComplete
				classes={{
					inputFocused: classes.input,
					clearIndicator: classes.input,
					popupIndicator: classes.input,
				}}
				className={classes.autoComplete}
				disabled={disabled}
				multiple={multiple}
				options={data}
				freeSolo={type !== "objectSelection"}
				value={multiple ? value || [] : getValue(value)}
				inputValue={searchText || ""}
				onInputChange={handleInputChange}
				{...(!multiple && {
					filterOptions: (options, params) => {
						const filtered = filter(options, params);
						// Suggest the creation of a new value
						if (searchText !== "" && canAddNew) {
							filtered.splice(0, 0, {
								inputValue: searchText,
								name: `Add "${searchText}"`,
							});
						}
						const showMore = options.find(o => o.id === 'studio_show_More_View');
						if(showMore && !['', null, undefined].includes(params.inputValue)) {
							const hasShowMore = filtered.findIndex(o => o.id === 'studio_show_More_View') !== -1;

							!hasShowMore && filtered.push({...showMore});
						}

						return filtered;
					},
				})}
				onChange={handleChange}
				renderInput={(params) => (
					<TextField
						{...params}
						variant="filled"
						label={camleCaseString(title || name)}
					/>
				)}
				getOptionLabel={(option) => {
					if(getOptionLabel) {
						return getOptionLabel(option, data);
					}
					return typeof option === "string" ? option : option[displayField] || ""
				}}
				// getOptionSelected={(option) => {
				// 	return typeof option === 'object' ? option[valueField] === value : option === value;
				// }}
			/>
			{error && <Typography className={classes.error}>{error}</Typography>}
			{moreDialog && <SearchView searchText={searchText}
				list={data}
				total={total}
				offset={offset}
				displayField={displayField}
				open={moreDialog}
				onNext={handleNext}
				onPrevious={handlePrevious}
				handleClose={handleClose}
				setSearchText={handleSearch}
				title={camleCaseString(title || name)}
				onChange={(value) => handleChange({fromDialog: true}, value)}  />}
		</React.Fragment>
	);
}
