import React, { useEffect } from "react";
import AxelorService from "./../services/axelor.rest";

const metaViewService = new AxelorService({
	model: "com.axelor.meta.db.MetaView",
});

function OneToMany({ attrs, id }) {
	const { gridView, targetModel: target, title, autoTitle, action } = attrs;
	const [gridItems, setGridItems] = React.useState([]);
	useEffect(() => {
		async function getGrid() {
			let viewName = gridView;
			if(action) {
				const _context = {
					_id: null,
				};
				const data = {
					action,
					model:  "com.axelor.meta.db.MetaAction",
					data: {
						context: _context,
					},
				}

				const actionResult = await metaViewService.action(data);
				if(actionResult.data && actionResult.data[0] && actionResult.data[0].view && actionResult.data[0].view.views) {
					const view = actionResult.data[0].view.views.find(v => v.type === 'grid');
					if(view) {
						viewName = view.name;
					}
				}
			}
			const payload = {
				model: target,
				data: {
					type: "grid",
					name: viewName,
				},
			};
			if(viewName) {
				metaViewService.view(payload).then((res) => {
					if (res.data && res.data.view) {
						const { items } = res.data.view;
						setGridItems(items);
					}
				});
			} else {
				setGridItems([]);
			}
		}
		getGrid();
	}, [gridView, target, id, action]);
	return (
		<div>
			<div className="one-to-many-header">
				<span>{title || autoTitle}</span>
				<div>
					<span>New</span>
				</div>
			</div>
			<div className="one-to-many-row">
				{gridItems.map((item, i) => (
					<span className="one-to-many-col one-to-many-col-border" key={i}>
						{item.autoTitle}
					</span>
				))}
			</div>
		</div>
	);
}

export default OneToMany;
