import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import FieldComponent from "../../components/FieldComponent";
import { useStore } from "../../store/context";
import Widget from "./../../components/Widget";
import { IDS } from "./../../constants";

const useStyles = makeStyles({
	container: {
		padding: "0px 25px",
		display: "flex",
		flexWrap: "wrap",
		alignItems: "center",
		justifyContent: "space-between",
	},
	message: {
		color: "#fff",
	},
	fieldContainer: {
		marginTop: 5,
		width: "45%",
	},
});

export default React.memo(function ExistingFieldsView(props) {
	const classes = useStyles();
	const { state } = useStore();
	const fields = state.metaFields || [];
	return (
		<Grid className={classes.container}>
			{fields ? (
				fields.map((field, i) => (
					<div className={classes.fieldContainer} key={i}>
						<Widget
							attrs={{ ...field, isCoreField: false }}
							design={true}
							component={FieldComponent}
							id={IDS.createWidgets.field}
							targetImageName={`${field && field.type.toLowerCase()}.png`}
						/>
					</div>
				))
			) : (
				<Typography className={classes.message}>No fields available</Typography>
			)}
		</Grid>
	);
});
