import React from "react";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import AxelorService from "./../services/axelor.rest";
import Utils, { generateCustomModelSchema } from "./../utils";
import Select from "./Select";
import {
	relationalFields,
	MODEL_TYPE,
	ENTITY_TYPE,
} from "./../constants";
import {
	fetchCustomFields,
	getEnableAppBuilder,
	fetchEntityModel,
	fetchEntityFields,
	fetchModelFields,
	fetchViews,
	fetchAttrsList,
	getTranslationList,
} from "./api";
import { modelSearch } from "./customModelSearch";
import {generateXpath} from './../store/xpathGenerator';
import convert from "xml-js";

const metaViewService = new AxelorService({
	model: "com.axelor.meta.db.MetaView",
});

const useStyles = makeStyles({
	container: {
		display: "flex",
	},
	autoComplete: {
		"& > div > label": {
			color: "#e7eaec !important",
			fontSize: 14,
		},
	},
	autoCompleteRoot: {
		width: 200,
		marginLeft: 10,
		color: "#e7eaec",
	},
	input: {
		color: "#e7eaec",
		fontSize: 14,
	},
});

const modelTypes = [
	{ name: "View", value: MODEL_TYPE.BASE },
	// { name: "Model", value: MODEL_TYPE.ENTITY },
	{ name: "Custom", value: MODEL_TYPE.CUSTOM },
];

function ViewSelection({
	model,
	update,
	modelType,
	modelField,
	startLoader,
	entityModel,
	entityType,
	metaFieldStore,
	clearHistory,
	...props
}) {
	const classes = useStyles();
	const modelName = `com.axelor.meta.db.${
		modelType !== MODEL_TYPE.CUSTOM ? "MetaModel" : "MetaJsonModel"
	}`;

	const fetchJSONFields = React.useCallback(
		(ids, record) => {
			const metaFieldService = new AxelorService({
				model: "com.axelor.meta.db.MetaJsonField",
			});
			const criteria = [];
			if (ids.length) {
				criteria.push({ fieldName: "id", operator: "in", value: ids });
				const data = {
					criteria,
				};
				metaFieldService.search({ data, sortBy: ["sequence"] }).then((res) => {
					if (res.data) {
						const list = res.data || [];
						const schema = generateCustomModelSchema(list, record);
						update((draft) => {
							draft.widgets = {
								...schema.widgets,
							};
							draft.items = schema.items;
							draft.loader = false;
						});
					} else {
						update((draft) => {
							draft.loader = false;
						});
					}
				});
			} else {
				update((draft) => {
					draft.widgets = {
						"-1": {
							type: "form",
							...record,
						},
						"0": { type: "dump_field", name: "" },
					};
					draft.items = [];
					draft.loader = false;
				});
			}
		},
		[update]
	);

	const handleEntityModelSelect = React.useCallback(
		async (model) => {
			startLoader();
			if (!model) {
				update((draft) => {
					clearHistory(draft);
					draft.items = [];
					draft.widgets = null;
					draft.initialItems = null;
					draft.entityModel = null;
					draft.loader = false;
					draft.model = null;
					draft.errorList = [];
					draft.attrsList = [];
					draft.translationList = [];
					draft.initialTranslationList = [];
					draft.removedTranslationList = [];
				});
				return;
			}
			const originalModelFields = await fetchModelFields(model);
			const metaModelFields = originalModelFields.map((f) => {
				return JSON.parse(
					JSON.stringify({
						...f,
						type: relationalFields[f.relationship] || (f.typeName || '').toLowerCase(),
						...(f.relationship && {
							targetModel: `${f.packageName}.${f.typeName}`,
						}),
						packageName: undefined,
					})
				);
			});
			fetchEntityModel(model).then((res) => {
				const schema = Utils.generateEntityModelSchema(res.entityXml, res);
				const fields = Utils.getEntityModelFields(res.entityXml);
				update((draft) => {
					clearHistory(draft);
					draft.model = model;
					draft.entityType = model.entityType;
					draft.actualType = model.actualType;
					draft.entityModel = model;
					draft.errorList = [];
					draft.attrsList = [];
					draft.translationList = [];
					draft.initialTranslationList = [];
					draft.removedTranslationList = [];
					if (modelType === MODEL_TYPE.ENTITY) {
						draft.widgets = schema.widgets;
						draft.items = schema.items;
						draft.metaFieldStore = [...metaModelFields];
						draft.metaFields = metaModelFields.filter(f => fields.findIndex(field => field.name === f.name) === -1);
					}
					if (modelType === MODEL_TYPE.BASE) {
						draft.metaFieldStore = [...fields, ...metaModelFields];
						draft.metaFields = [...fields, ...metaModelFields];
					}
					draft.loader = false;
				});
			});
		},
		[update, startLoader, modelType, clearHistory]
	);

	const handleModelSelect = React.useCallback(
		async (model) => {
			const metaModelService = new AxelorService({ model: modelName });
			if (!model) {
				update((draft) => {
					clearHistory(draft);
					draft.model = null;
					draft.items = [];
					draft.exView = null;
					draft.view = null;
					draft.widgets = null;
					draft.initialItems = null;
					draft.initialWidgets = null;
					draft.customFieldWidgets = null;
					draft.customFieldItems = null;
					draft.customFields = [];
					draft.modelField = null;
					draft.extensionXML = null;
					draft.extensionView = null;
					draft.originalXML = null;
					draft.selectedView = null;
					draft.metaFields = [];
					draft.metaFieldStore = [];
					draft.extensionMoves = [];
					draft.errorList = [];
					draft.attrsList = [];
					draft.translationList = [];
					draft.initialTranslationList = [];
					draft.removedTranslationList = [];
				});
				return;
			}
			let data = {
				fields: ["metaFields", "packageName", "name", "fullName"],
				related: {
					metaFields: [
						"typeName",
						"label",
						"mappedBy",
						"relationship",
						"name",
						"packageName",
					],
				},
			};
			if (modelType === MODEL_TYPE.CUSTOM) {
				update((draft) => {
					draft.loader = true;
				});
				data = {
					related: {
						menuBuilder: ["title", "parentMenu"],
					},
				};
			}
			const entityFields = await fetchEntityFields(model);
			if(model.entityType === ENTITY_TYPE.ENTITY) {
				handleEntityModelSelect(model);
			} else {
				metaModelService.fetch(model.id, data).then((res) => {
					if (res.data) {
						const record = res.data[0];
						const { fields = [], ...rest } = record;
						update((draft) => {
							clearHistory(draft);
							draft.model = model;
							draft.entityType = model.entityType;
							draft.widgets = null;
							draft.initialWidgets = null;
							draft.items = [];
							draft.initialItems = null;
							draft.view = null;
							draft.exView = null;
							draft.modelField = null;
							draft.customFields = [];
							draft.customFieldWidgets = null;
							draft.originalXML = null;
							draft.extensionXML = null;
							draft.extensionView = null;
							draft.entityFields = entityFields;
							draft.extensionMoves = [];
							draft.errorList = [];
							draft.attrsList = [];
							draft.translationList = [];
							draft.initialTranslationList = [];
							draft.removedTranslationList = [];
							draft.selectedView = null;
							draft.editWidget = -1;
							draft.editWidgetType = null;
							draft.tabIndex = 0;
							const _fields = (record.metaFields || []).map((f) => {
								return JSON.parse(
									JSON.stringify({
										...f,
										type: relationalFields[f.relationship] || (f.typeName || '').toLowerCase(),
										...(f.relationship && {
											targetModel: `${f.packageName}.${f.typeName}`,
										}),
										packageName: undefined,
									})
								);
							});
							if (modelType === MODEL_TYPE.BASE) {
								draft.metaFieldStore = [..._fields, ...entityFields];
								draft.metaFields = [..._fields, ...entityFields];
							} else if (modelType === MODEL_TYPE.ENTITY) {
								const modelRecord = {
									name: record.name,
									packageName: record.packageName,
									model: `${record.packageName}.${record.name}`,
								};
								const schema = generateCustomModelSchema([], modelRecord);
								draft.widgets = {
									...schema.widgets,
								};
								draft.items = schema.items;
								draft.loader = false;
								draft.entityModel = null;
								if (model.entityType === ENTITY_TYPE.META) {
									draft.metaFieldStore = [..._fields];
									draft.metaFields = [..._fields];
								}
							} else {
								fetchJSONFields(
									fields.map((f) => f.id),
									rest
								);
								draft.customModel = record;
							}
						});
					}
				});
			}
		},
		[update, modelName, modelType, fetchJSONFields, handleEntityModelSelect, clearHistory]
	);

	const handleViewSelect = React.useCallback(
		async (view) => {
			startLoader();
			if (!view) {
				update((draft) => {
					clearHistory(draft);
					draft.items = [];
					draft.exView = null;
					draft.view = null;
					draft.widgets = null;
					draft.initialItems = null;
					draft.initialWidgets = null;
					draft.customFieldWidgets = null;
					draft.customFieldItems = null;
					draft.customFields = [];
					draft.modelField = null;
					draft.extensionXML = null;
					draft.extensionView = null;
					draft.originalXML = null;
					draft.loader = false;
					draft.metaFields = draft.metaFieldStore;
					draft.selectedView = null;
					draft.extensionMoves = [];
					draft.errorList = [];
					draft.attrsList = [];
					draft.translationList = [];
					draft.initialTranslationList = [];
					draft.removedTranslationList = [];
				});
				return;
			}
			const views = await fetchViews(view);
			const attrsList = await fetchAttrsList({model: model.name, view: view.name});
			const schema = Utils.generateXMLToViewSchema({view: views.view, fields: metaFieldStore, extensionXML: views.extensionXML, attrsList});
			const translationNames = Object.values(schema.widgets).filter(widget => widget.title && widget.title.startsWith("studio:")).map(widget => widget.title);
			const translationList = await getTranslationList(translationNames);
			const originalViewData = {
				operator: "and",
				criteria: [
					{
						operator: "or",
						criteria: [
							{ fieldName: "computed", operator: "=", value: false },
							{ fieldName: "computed", operator: "isNull" },
						],
					},
					{
						operator: "or",
						criteria: [
							{ fieldName: "extension", operator: "=", value: false },
							{ fieldName: "extension", operator: "isNull" },
						],
					},
					{ fieldName: "name", operator: "=", value: `${view.name}` },
				],
			};
			metaViewService.search({ data: originalViewData }).then((res) => {
				const { data = [] } = res;
				if (data[0]) {
					update((draft) => {
						draft.originalXML = JSON.parse(
							convert.xml2json(data[0].xml, {
								compact: false,
								fullTagEmptyElement: false,
							})
						);
					});
				}
			});
			// fetch studio extension view
			const _viewSearch = {
				_domain: `self.xmlId = 'studio-${view.name}' and self.extension = true`,
			};
			const res = await metaViewService.search({ data: _viewSearch });
			const { data: resData = [] } = res;
			const record = resData[0];
			if (record) {
				update((draft) => {
					draft.extensionView = record;
					draft.extensionXML = JSON.parse(
						convert.xml2json(record.xml, {
							compact: false,
							fullTagEmptyElement: false,
						})
					);
				});
			}
			update((draft) => {
				clearHistory(draft);
				draft.attrsList = attrsList;
				draft.translationList = translationList;
				draft.initialTranslationList = translationList;
				draft.removedTranslationList = [];
				if (schema) {
					draft.widgets = schema.widgets;
					draft.initialWidgets = schema.widgets;
					draft.items = schema.items;
					draft.initialItems = schema.items;
					draft.metaFields = [...draft.metaFieldStore].filter(
						(field) =>
							schema.fieldList.findIndex((f) => f.name === field.name) ===
							-1
					);
				}
				draft.selectedView = view;
				if (views.view) {
					draft.view = { fields: draft.metaFields, view: views.view };
					draft.exView = views.view;
				}
				draft.customFields = [];
				draft.customFieldWidgets = null;
				draft.customFieldItems = null;
				draft.modelField = null;
				draft.loader = false;
				draft.extensionMoves = [];
				draft.errorList = [];
				generateXpath(draft);
			});
		},
		[update, startLoader, metaFieldStore, clearHistory, modelName]
	);

	const viewFilter = React.useCallback(() => {
		let _domain = `self.type='form'`;
		let _model = `${model ? model.fullName : ""}`;
		if (entityType === ENTITY_TYPE.ENTITY) {
			_model = `${entityModel ? Utils.getEntityModel(entityModel) : ""}`;
		}
		// if(model) {
		_domain = `${_domain} and self.model='${_model}' and (self.computed = true OR self.name NOT IN (select meta.name from MetaView meta where meta.computed = true))`;
		// }
		return {
			_domain,
			fields: [
				"name",
				"model",
				"title",
				"type",
				"extension",
				"xmlId",
				"computed",
				"xml",
			],
		};
	}, [model, entityModel, entityType]);

	const modelFilter = React.useCallback(() => {
		return {
			fields: ["name", "fullName", "packageName"],
		};
	}, []);

	const metaFieldFilter = React.useCallback(() => {
		const _domain = `self.json = true and self.metaModel.id='${
			model ? model.id : -1
		}'`;
		return {
			_domain,
			fields: ["name"],
		};
	}, [model]);

	const handleMetaFieldSelect = React.useCallback(
		(field) => {
			if (field) {
				fetchCustomFields(field, model).then((res) => {
					const { data = [] } = res;
					const schema = generateCustomModelSchema(data, undefined, '', 'customForm');
					update((draft) => {
						draft.customFieldWidgets = schema.widgets;
						draft.customFieldItems = schema.items;
						draft.customFields = data;
						draft.modelField = field;
					});
				});
			} else {
				update((draft) => {
					draft.customFieldWidgets = null;
					draft.customFieldItems = null;
					draft.customFields = [];
					draft.modelField = field;
				});
			}
		},
		[model, update]
	);

	const handleTypeSelect = React.useCallback(
		(type) => {
			const { value } = type;
			if (value === MODEL_TYPE.CUSTOM) {
				getEnableAppBuilder().then((res) => {
					update((draft) => {
						draft.enableAppBuilder = res;
					});
				});
			}
			update((draft) => {
				clearHistory(draft);
				draft.modelType = type ? type.value : null;
				draft.entityType = null;
				draft.model = null;
				draft.widgets = null;
				draft.initialWidgets = null;
				draft.items = [];
				draft.initialItems = null;
				draft.view = null;
				draft.exView = null;
				draft.metaFields = [];
				draft.modelField = null;
				draft.customFields = [];
				draft.customFieldWidgets = null;
				draft.entityModel = null;
				draft.metaFields = [];
				draft.metaFieldStore = [];
				draft.selectedView = null;
				draft.extensionMoves = [];
				draft.errorList = [];
				draft.attrsList = [];
				draft.translationList = [];
				draft.initialTranslationList = [];
				draft.removedTranslationList = [];
			});
		},
		[update, clearHistory]
	);

	const handleViewFilterData = React.useCallback((list) => {
		const newList = list.filter((view) => {
			if (!view.computed) {
				const index = list.findIndex((e) => e.name === view.name && e.computed);
				if (index !== -1) {
					return false;
				}
			}
			if(view.extension === true) {
				return false;
			}
			return true;
		});
		return newList;
	}, []);

	const getOptionLabel = (option) => {
		if (option.extension === true) {
			return `${option.name} (Extension)`;
		}
		if (option.computed === true) {
			return `${option.name} (Computed)`;
		}
		return option.name;
	};
	return (
		<Grid className={classes.container}>
			<Select
				options={modelTypes}
				value={modelTypes.find((e) => e.value === modelType)}
				filterSelectedOptions={true}
				label="Type"
				onChange={handleTypeSelect}
				disableClearable
			/>
			<Select
				onChange={handleModelSelect}
				label="Model"
				model={modelName}
				searchFilter={modelFilter}
				value={model}
				onSearch={modelSearch}
				modelType={modelType}
			/>
			{modelType === MODEL_TYPE.BASE && (
				<React.Fragment>
					<Select
						onChange={handleViewSelect}
						label="View"
						model="com.axelor.meta.db.MetaView"
						searchFilter={viewFilter}
						getOptionLabel={getOptionLabel}
						value={props.selectedView}
						filterData={handleViewFilterData}
					/>
					<Select
						label="Custom Fields"
						model="com.axelor.meta.db.MetaField"
						searchFilter={metaFieldFilter}
						onChange={handleMetaFieldSelect}
						value={modelField}
					/>
				</React.Fragment>
			)}
		</Grid>
	);
}

export default React.memo(ViewSelection);
