import React from "react";
import { Grid, IconButton } from "@material-ui/core";
import { Add, Save, Delete, Undo, Redo, Menu } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";

import ViewSelection from "./ViewSelection";
import { MODEL_TYPE } from "./../constants";

const useStyles = makeStyles({
	toolbarContainer: {
		width: "100%",
		height: "100%",
		backgroundColor: "rgb(41, 56, 70)",
		paddingLeft: 15,
		'.modern-dark &': {
			backgroundColor: "#1b1b1b",
		}
	},
	toolItemView: {
		color: "#fff",
	},
	crudIcon: {
		height: "1em",
		width: "1em",
		cursor: "pointer",
	},
	selectionContainer: {
		flex: 1,
	},
});

function isStudioView(view) {
	if (view && view.xmlId && view.xmlId.indexOf("studio-") === 0) {
		return true;
	}
	return false;
}

function Toolbar({
	update,
	saveView,
	modelType,
	saveCustomFieldView,
	removeView,
	model,
	onNew,
	...props
}) {
	const classes = useStyles();
	const save = React.useCallback(() => {
		saveView();
		if (modelType === MODEL_TYPE.BASE) {
			saveCustomFieldView();
		}
	}, [saveView, saveCustomFieldView, modelType]);
	const isRemoveDisabled = !(
		props.extensionView ||
		isStudioView(props.selectedView) ||
		(modelType === MODEL_TYPE.CUSTOM && Boolean(props.customModel)) ||
		(modelType === MODEL_TYPE.ENTITY &&
			Boolean(props.entityModel) &&
			Boolean(props.entityModel.id))
	);
	const isNewDisabled =
		modelType === MODEL_TYPE.BASE && !model && !props.entityModel;
	const isSaveDisabled = Object.keys(props.errorList).length > 0;

	return (
		<Grid container className={classes.toolbarContainer}>
			<IconButton
				className={classes.toolItemView}
				onClick={props.onSidePanelToggle}
			>
				<Menu className={classes.crudIcon} />
			</IconButton>
			{modelType !== MODEL_TYPE.BASE && <IconButton
				className={classes.toolItemView}
				onClick={onNew}
				disabled={isNewDisabled}
			>
				<Add className={classes.crudIcon} />
			</IconButton>}
			<IconButton
				className={classes.toolItemView}
				onClick={save}
				disabled={isSaveDisabled}
			>
				<Save className={classes.crudIcon} />
			</IconButton>
			<IconButton
				className={classes.toolItemView}
				onClick={removeView}
				disabled={isRemoveDisabled}
			>
				<Delete className={classes.crudIcon} />
			</IconButton>
			<Grid item className={classes.selectionContainer}>
				<ViewSelection
					model={model}
					update={update}
					modelType={modelType}
					{...props}
				/>
			</Grid>
			<Grid>
				<IconButton
					className={classes.toolItemView}
					onClick={() => props.undo()}
					disabled={props.pastLength <= 0}
				>
					<Undo className={classes.crudIcon} />
				</IconButton>
				<IconButton
					className={classes.toolItemView}
					onClick={() => props.redo()}
					disabled={props.futureLength <= 0}
				>
					<Redo className={classes.crudIcon} />
				</IconButton>
			</Grid>
		</Grid>
	);
}

export default React.memo(Toolbar);
