/** Overview Fields */
export const Name = ({ ...props } = {}) =>
	StringField({
		name: "name",
		required: true,
		readOnly: true,
		regex: /^[a-z0-9]+$/i,
		unique: true,
		isDisabled: ({properties, metaFieldStore, editWidgetType, modelType}) => {
			if(modelType === MODEL_TYPE.BASE && editWidgetType !== 'customField') {
				// const name = properties['name'];
				// const index = metaFieldStore.findIndex(field => field.name === name);
				// return index !== -1 ? true : false;
				return true;
			}
			return false;
		},
		requiredFor: ['button','label', 'panel', 'string', 'panel-dashlet', 'separator', 'panel-stack', 'integer', 'date', 'time', 'datetime', 'decimal', 'one-to-many', 'many-to-many', 'boolean', 'label', 'viewer', 'editor', 'one-to-one', 'spacer', 'long', 'hilite'],
		...props,
	});

export const Type = StringField({
	name: "type",
	column: "type_name",
	required: true,
	selection: "json.field.type",
	readOnly: "true",
	formatter: (value, properties) => {
		if(properties.tab) {
			return 'panel-tabs';
		}
		const entityIndex = value.indexOf('entity');
		if(entityIndex !== -1) {
			return value.substring(0, entityIndex);
		}
		return value;
	}
});


