const PropertiesContext = React.createContext();

function Label(text, field) {
	const classes = useStyles();
	return (
		<span>
			<span>{text}</span>
			{field.unique && <span className={classes.unique}> (unique) </span>}
			{field.required && <span className={classes.required}> * </span>}
		</span>
	);
}

const isValueGenerated = (value) => {
	return value.includes("studio:");
};

const hasPropertyToShow = (list, type) => {
	return list.filter(item => !item.dependModelType || item.dependModelType === type).length > 0;
}

function StringInput(_props) {
	const { index, multiline = false, error, parentPanel, onTranslationRemove, clearable = false } = _props;
	const classes = useStyles();
	const [confirm, setConfirm] = React.useState(false);
	const {
		title,
		name,
		type,
		required,
		readOnly,
		max,
		min,
		parentField,
		translationView,
		...rest
	} = _props.field;
	const props = React.useContext(PropertiesContext);
	const {
		propertyList,
		setPropertyList,
		onChange,
		modelType,
		metaFieldStore,
		editWidgetType,
	} = props;
	let label = Label(translate(camleCaseString(title || name)), _props.field);
	let placeholder = translate(camleCaseString(title || name));
	if (name === "showIf" || name === "requiredIf") {
		placeholder = "id == 1";
	} else if (name === "hideIf") {
		placeholder = "id == null";
	} else if (name === "readOnlyIf") {
		placeholder = "id != null";
	}
	let value = propertyList[name] || "";
	if (
		(modelType === rest.modelType || props.editWidgetType === "customField") &&
		parentField
	) {
		const field = propertyList[parentField]
			? JSON.parse(propertyList[parentField])
			: {};
		value = field[name] || "";
	}
	if (rest.formatter) {
		value = rest.formatter(value, propertyList);
	}
	let disabled = Boolean(readOnly);
	if (rest.isDisabled) {
		disabled = rest.isDisabled({
			properties: propertyList,
			metaFieldStore,
			editWidgetType,
			modelType,
			parentPanel,
		});
	}

	const handleTranslationClick = React.useCallback(() => {
		if (isValueGenerated(value)) {
			return;
		}
		const newValue = generateFieldUniqueTitle({
			name,
			fieldName: propertyList[name] || type,
		});
		onChange(
			{
				...(propertyList || {}),
				...getProperty(
					name,
					newValue,
					parentField,
					propertyList[parentField],
					(!(modelType === rest.modelType)  || editWidgetType === 'customField')
				),
			},
			name
		);
	}, [
		propertyList,
		name,
		type,
		value,
		onChange,
		rest.modelType,
		modelType,
		parentField,
		editWidgetType
	]);

	const handleTranslationRemove = React.useCallback(() => {
		setConfirm(false);
		onChange(
			{
				...(propertyList || {}),
				...getProperty(
					name,
					"",
					parentField,
					propertyList[parentField],
					!(modelType === rest.modelType)
				),
			},
			name
		);
		onTranslationRemove(value);
	}, [onTranslationRemove, value, name, parentField, modelType, onChange, propertyList, rest.modelType]);

	const removeTranslation = React.useCallback(() => {
		setConfirm(true);
	}, []);

	const onClose = React.useCallback(() => {
		setConfirm(false);
	}, []);

	return (
		<React.Fragment>
			<TextField
				error={Boolean(error)}
				helperText={error}
				InputProps={{
					classes: {
						root: classes && classes.outlinedRoot,
						notchedOutline: classes && classes.notchedOutline,
						focused: classes && classes.focused,
						disabled: classes && classes.disableInput,
					},
					endAdornment: translationView ? (
						isValueGenerated(value) ? (
							<IconButton
								className={classes.translateButton}
								onClick={removeTranslation}
							>
								<CloseIcon />
							</IconButton>
						) : (
							<IconButton
								className={classes.translateButton}
								onClick={handleTranslationClick}
							>
								<TranslateIcon />
							</IconButton>
						)
					) : clearable ?
					<IconButton
								className={classes.translateButton}
								onClick={() => {
									let value = '';
									let flag = true;
									if (
										rest.modelType === MODEL_TYPE.BASE &&
										props.editWidgetType !== "customField"
									) {
										flag =
											value === ""
												? propertyList[name] !== undefined
													? true
													: false
												: true;
									}
									if (
										rest.modelType === MODEL_TYPE.CUSTOM &&
										props.editWidgetType === "customField"
									) {
										// set null value when value is negative for custom view & custom fields
										value = value || null;
									}
									if (flag) {
										onChange(
											{
												...(propertyList || {}),
												...getProperty(
													name,
													value,
													parentField,
													propertyList[parentField],
													(!(modelType === rest.modelType)  || editWidgetType === 'customField')
												),
											},
											name
										);
									}
								}}
							>
								<ClearIcon />
							</IconButton>
					: null,
				}}
				inputProps={{ min: min, max: max }}
				InputLabelProps={{
					className: classes && classes.label,
				}}
				key={index}
				className={classes && classes.input}
				id="outlined-basic"
				multiline={multiline}
				type={type === "integer" ? "number" : type}
				label={label}
				placeholder={placeholder}
				variant="outlined"
				disabled={disabled || (translationView && isValueGenerated(value))}
				value={value}
				rows={3}
				onChange={(e) => {
					if (type === "integer" && isNaN(Number(e.target.value))) {
						return;
					}
					if (min && max) {
						const value = Number(e.target.value);
						if (value < min || value > max) {
							return;
						}
					}
					let value = e.target.value;
					setPropertyList({
						...(propertyList || {}),
						...getProperty(
							name,
							value,
							parentField,
							propertyList[parentField],
							(!(modelType !== rest.modelType)  || editWidgetType === 'customField')
						),
					});
				}}
				onBlur={(e) => {
					if (type === "integer" && isNaN(Number(e.target.value))) {
						return;
					}
					if (min && max) {
						const value = Number(e.target.value);
						if (value < min || value > max) {
							return;
						}
					}
					let value = e.target.value;
					let flag = true;
					if (
						rest.modelType === MODEL_TYPE.BASE &&
						props.editWidgetType !== "customField"
					) {
						flag =
							value === ""
								? propertyList[name] !== undefined
									? true
									: false
								: true;
					}
					if (
						rest.modelType === MODEL_TYPE.CUSTOM &&
						props.editWidgetType === "customField"
					) {
						// set null value when value is negative for custom view & custom fields
						value = value || null;
					}
					if (flag) {
						onChange(
							{
								...(propertyList || {}),
								...getProperty(
									name,
									value,
									parentField,
									propertyList[parentField],
									(!(modelType === rest.modelType)  || editWidgetType === 'customField')
								),
							},
							name
						);
					}
				}}
			/>
			{translationView && isValueGenerated(value) && (
				<TranslationGrid
					InputProps={{
						classes: {
							root: classes && classes.outlinedRoot,
							notchedOutline: classes && classes.notchedOutline,
							focused: classes && classes.focused,
							disabled: classes && classes.disableInput,
							input: classes && classes.translationInput,
						},
					}}
					InputLabelProps={{
						className: classes && classes.label,
					}}
					className={classes && classes.translationInputView}
					keyValue={value}
				/>
			)}
			{
				confirm &&
				<DialogConfirmation
					open={confirm}
					message="Are you sure, do you want to delete translations?"
					onClose={onClose}
					onOk={handleTranslationRemove}
				/>
			}
		</React.Fragment>
	);
}

function BooleanField(_props) {
	const { index,  } = _props;
	let { name, title, parentField, defaultValue, ...rest } = _props.field;
	const props = React.useContext(PropertiesContext);
	const { propertyList, setPropertyList, onChange, modelType, editWidgetType, metaFieldStore } = props;
	let _value = getPropertyValue(
		propertyList,
		name,
		parentField,
		defaultValue,
		(!(modelType !== rest.modelType) || editWidgetType === 'customField')
	);
	let fieldValue = Boolean(_value ? !(_value === "false") : _value);
	const classes = useStyles();
	if (rest.getValue) {
		fieldValue = rest.getValue(propertyList);
	}
	let disabled = false;
	if(rest.isDisabled) {
		disabled = rest.isDisabled({
			properties: propertyList,
			metaFieldStore,
			editWidgetType,
			modelType,
		});
	}
	title = translate(camleCaseString(title || name));
	return (
		<FormControlLabel
			style={{ marginBottom: 15 }}
			key={index}
			control={
				<Switch
					classes={{
						switchBase: classes && classes.switchBase,
						track: classes && classes.track,
						checked: classes && classes.checked,
					}}
					checked={fieldValue}
					disabled={disabled}
					onChange={() => {
						setPropertyList({
							...propertyList,
							...(rest.change ? rest.change(!fieldValue, propertyList) : {}),
							...getProperty(
								name,
								!fieldValue,
								parentField,
								propertyList[parentField],
								(!(modelType !== rest.modelType) || editWidgetType === 'customField')
							),
						});
						onChange(
							{
								...propertyList,
								...(rest.change ? rest.change(!fieldValue, propertyList) : {}),
								...getProperty(
									name,
									!fieldValue,
									parentField,
									propertyList[parentField],
									(!(modelType !== rest.modelType) || editWidgetType === 'customField')
								),
							},
							name
						);
					}}
					value={propertyList[name]}
				/>
			}
			label={camleCaseString(title || name)}
		/>
	);
}

function StaticSelection(_props) {
	const { index } = _props;
	let {
		name,
		required,
		data,
		helper,
		title,
		color,
		parentField,
		defaultOption = true,
		...rest
	} = _props.field;
	const props = React.useContext(PropertiesContext);
	const {
		propertyList,
		setPropertyList,
		labelWidth,
		inputLabel,
		type,
		onChange,
		modelType,
	} = props;
	const classes = useStyles();

	if (typeof data === "object" && !Array.isArray(data)) {
		data = data[(typeReplacer[type] || type).toLowerCase()] || [];
	}
	title = translate(camleCaseString(title || name));

	const ColorTag = ({ color }) => (
		<div style={{ width: 15, height: 15, backgroundColor: color }}></div>
	);
	let value = propertyList[name] || "";
	if (
		(modelType === rest.modelType || props.editWidgetType === "customField") &&
		parentField
	) {
		const field = propertyList[parentField]
			? JSON.parse(propertyList[parentField])
			: {};
		value = field[name] || "";
	}
	return (
		<FormControl key={index} variant="outlined" className={classes.formControl}>
			<InputLabel ref={inputLabel} className={classes.focused}>
				{camleCaseString(title || name)}
			</InputLabel>
			<Select
				value={value}
				labelWidth={labelWidth || 0}
				style={{ width: "100%", marginBottom: 15 }}
				className={classes.selection}
				classes={{
					iconOutlined: classes.iconOutlined,
					root: classes.selection,
				}}
				required={required}
				variant="outlined"
				onChange={(e) => {
					setPropertyList({
						...propertyList,
						...getProperty(
							name,
							e.target.value,
							parentField,
							propertyList[parentField],
							modelType === rest.modelType ||
								props.editWidgetType === "customField"
						),
					});
					onChange(
						{
							...propertyList,
							...getProperty(
								name,
								e.target.value,
								parentField,
								propertyList[parentField],
								modelType === rest.modelType ||
									props.editWidgetType === "customField"
							),
						},
						name
					);
				}}
			>
				{defaultOption && <MenuItem
					key={index}
					value={null}
					style={{ padding: "15px 0px" }}
				></MenuItem>}
				{data &&
					data.map((opt, index) =>
						typeof opt === "object" ? (
							<MenuItem key={index} value={opt.value}>
								<i className={`fa ${opt}`} style={{ marginRight: 4 }} />
								{opt.text}
							</MenuItem>
						) : (
							<MenuItem key={index} value={opt}>
								{color && <ColorTag color={opt} />}
								<i className={`fa ${opt}`} style={{ marginRight: 4 }} />
								<span>{opt}</span>
							</MenuItem>
						)
					)}
			</Select>
			{helper && (
				<Icon
					type="search"
					className="helper"
					onClick={() => window.open(helper, "_blank")}
				/>
			)}
		</FormControl>
	);
}

function checkDepend(field, propertyList) {
	if (field.depends) {
		const value = propertyList[field.depends];
		if (field.dependValue !== value) {
			return false;
		}
		return true;
	}
	return;
}

function checkModel(field, modelType, editWidgetType, propertyList) {
	if (field.dependModelType && field.dependModelType !== modelType) {
		if (editWidgetType !== "customField") {
			return false;
		}
	}
	if(editWidgetType === "customField" && field.showInCustomField === false) {
		return false;
	}
	if(field.shouldRender && !field.shouldRender(propertyList)) {
		return false;
	}
	return true;
}

function RenderPropertyField() {
	const props = React.useContext(PropertiesContext);
	const {
		elements,
		id,
		propertyList,
		enableAppBuilder,
		modelType,
		editWidgetType,
		errors,
		entityType,
		actualType,
		parentPanel,
		onTranslationRemove,
	} = props;
	return (
		<>
			{elements.map((field, index) => {
				const depend = checkDepend(field, propertyList);
				if (depend === false) {
					return null;
				}
				if (!checkModel(field, modelType, editWidgetType, propertyList)) {
					return null;
				}
				if (field.name === "appBuilder" && !enableAppBuilder) {
					return null;
				}
				if (
					[actualType, entityType].includes(ENTITY_TYPE.META) &&
					field.name === "packageName"
				) {
					return null;
				}
				const key = `${index}_${id}`;
				switch (field.type) {
					case "string":
						return (
							<StringInput
								field={field}
								key={key}
								index={key}
								multiline={false}
								error={errors[field.name]}
								onTranslationRemove={onTranslationRemove}
							/>
						);
					case "integer":
						return (
							<StringInput
								field={field}
								key={key}
								index={key}
								multiline={false}
								error={errors[field.name]}
								parentPanel={parentPanel}
								clearable={true}
							/>
						);
					case "boolean":
						return (
							<BooleanField
								field={field}
								key={key}
								index={key}
								error={errors[field.name]}
							/>
						);
					case "select":
					case "objectSelection":
						return (
							<SelectComponent
								field={field}
								key={key}
								index={key}
								props={props}
								error={errors[field.name]}
							/>
						);
					case "staticSelect":
						return (
							<StaticSelection
								field={field}
								key={key}
								index={key}
								error={errors[field.name]}
							/>
						);
					case "text":
						return (
							<StringInput
								field={field}
								key={key}
								index={key}
								multiline={true}
								error={errors[field.name]}
							/>
						);
					default:
						return (
							<StringInput
								field={field}
								key={key}
								index={key}
								error={errors[field.name]}
							/>
						);
				}
			})}
		</>
	);
}

function PropertiesProvider({ children, ...value }) {
	return (
		<PropertiesContext.Provider value={value}>
			{children}
		</PropertiesContext.Provider>
	);
}

export default function Properties(props) {
	const { state, onWidgetChange, onTranslationRemove } = useStore();
	const {
		widgets,
		editWidget,
		editWidgetType,
		customFieldWidgets,
		modelType,
		enableAppBuilder,
		metaFieldStore,
		errorList = {},
		entityType,
		actualType,
	} = state;
	let widget = null;
	if (editWidget) {
		if (editWidgetType === "customField" && customFieldWidgets) {
			widget = customFieldWidgets[editWidget];
		} else if (editWidgetType !== "customField" && widgets) {
			widget = widgets[editWidget];
		}
	}
	const { serverType, isSelectionField } = widget || {};
	let { type } = widget || {};
	let selectedType =
		serverType === "field" ? type || "string" : serverType || type;
	selectedType = selectedType && selectedType.toLowerCase().replace(/_/g, "-");
	if (selectedType === "datetime") {
		const field = metaFieldStore.find((f) => f.name === widget.name);
		if (field && `${field.tz}` === "true") {
			selectedType = "zoneddatetime";
		}
	}
	const [propertyList, setPropertyList] = useState({});
	let property = options.find((option) => option.type === selectedType);
	if (selectedType === "form") {
		if (state.modelType === MODEL_TYPE.CUSTOM) {
			property = options.find((option) => option.type === "modelForm");
		} else if (state.modelType === MODEL_TYPE.ENTITY) {
			property = options.find((option) => option.type === "entityForm");
		}
	} else if (
		selectedType &&
		modelType === MODEL_TYPE.ENTITY &&
		(selectedType || "").toLowerCase().indexOf("entity") === -1
	) {
		selectedType = `${selectedType}entity`;
		property = options.find((option) => option.type === selectedType);
	}
	if(isSelectionField) {
		const typeFieldIndex = property.value.overview.findIndex(item => item.name === 'type');
		property.value.overview.splice(typeFieldIndex, 1, SelectableType);
	}
	const classes = useStyles();
	const inputLabel = React.useRef(null);
	const [labelWidth, setLabelWidth] = React.useState(0);
	React.useEffect(() => {
		setPropertyList({
			...(widget || {}),
			type: selectedType,
		});
		setLabelWidth(
			inputLabel && inputLabel.current && inputLabel.current.offsetWidth
		);
	}, [widget, selectedType]);

	async function onChange(props, changedPropertyName) {
		const newProperties = { ...props };
		if(changedPropertyName === JsonRelationalField.name) {
			// when isJsonRelationalField changes
			newProperties[TargetJsonModel.name] = null;
			newProperties[TargetModel.name] = null;
		}
		if (changedPropertyName !== JsonRelationalField.name && changedPropertyName !== 'type') {
			newProperties.type = widget.type;
		}
		/* selectionText implementation starts here */
			if(newProperties.updateSelection) {
				if(!widget.updateSelection) {
					const obj = {name: newProperties.selection};
					const text = await getSelectionText(obj);
					newProperties.selectionText = text;
				}
			} else {
				newProperties.selectionText = '';
			}
			if(newProperties.selection && typeof newProperties.selection === 'object') {
				newProperties.selection = newProperties.selection.name;
			}
		/* selectionText implementation ends here */
		onWidgetChange(editWidget, { ...newProperties });
	}

	type = selectedType;
	let errors = {};
	if (editWidget && errorList) {
		errors = errorList[editWidget] || {};
	}
	const parentPanel = Object.values(widgets || {}).find(
		(w) => w.items && w.items.indexOf(editWidget) !== -1
	);
	if(widget?.type === 'panel-tabs' && editWidgetType === "customField") {
		return null;
	}
	if (
		property?.dependModelType &&
		`${widget?.tab}` === "true" &&
		property?.dependModelType !== modelType
	) {
		return null;
	}
	return (
		<Grid style={{ padding: 20 }}>
			{type &&
				property &&
				!(
					state.modelType === MODEL_TYPE.CUSTOM && property.type === TYPE.tabs
				) &&
				Object.keys(property.value).map((panel, i) => (
					<Grid key={i}>
						<Typography variant="body1" className={classes.panelTitle}>
							{hasPropertyToShow(property.value[panel], modelType) && camleCaseString(panel)}
						</Typography>
						<Grid
							style={{ display: "flex", flexDirection: "column", width: "90%" }}
						>
							<PropertiesProvider
								key={editWidget}
								elements={property.value[panel]}
								propertyList={propertyList}
								setPropertyList={setPropertyList}
								labelWidth={labelWidth}
								inputLabel={inputLabel}
								type={type}
								onWidgetChange={onWidgetChange}
								id={editWidget}
								onChange={onChange}
								modelType={modelType}
								enableAppBuilder={enableAppBuilder}
								editWidgetType={editWidgetType}
								metaFieldStore={metaFieldStore}
								errors={errors}
								entityType={entityType}
								actualType={actualType}
								parentPanel={parentPanel}
								onTranslationRemove={onTranslationRemove}
							>
								<RenderPropertyField />
							</PropertiesProvider>
						</Grid>
					</Grid>
				))}
		</Grid>
	);
}
