import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import { fieldTypes, modelFields, entityFields } from "../../fields";
import FieldComponent from "../../components/FieldComponent";
import Widget from "./../../components/Widget";
import ExistingFieldsView from "./ExistingFieldsView";
import { useStore } from "../../store/context";
import { MODEL_TYPE, TYPE } from "../../constants";

const useStyles = makeStyles({
	container: {
		padding: "0px 25px",
		display: "flex",
		flexWrap: "wrap",
		alignItems: "center",
		justifyContent: "space-between",
	},
	fieldGroupTitle: {
		padding: "25px 25px 0px 25px",
		color: "white",
	},
	fieldContainer: {
		marginTop: 5,
		width: "45%",
	},
});

export default React.memo(function AddPanelView() {
	const classes = useStyles();
	const { state } = useStore();
	const fields =
		state.modelType === MODEL_TYPE.ENTITY
			? entityFields
			: [...fieldTypes, ...modelFields].filter(e => {
				if(e.name === 'Menu' && state.modelType === MODEL_TYPE.CUSTOM) {
					return false;
				}
				return true;
			});

	const showField = (field) => {
		if([TYPE.menubar, TYPE.toolbar].includes(field.type) && state.widgets) {
			const index = Object.values(state.widgets).findIndex(w => w.type === field.type);
			return index === -1;
		}
		return true;
	}
	return (
		<>
			{fields.map((fieldType, index) => (
				<React.Fragment key={index}>
					<Typography variant="body1" className={classes.fieldGroupTitle}>
						{fieldType.name}
					</Typography>
					<Grid className={classes.container}>
						{fieldType.value &&
							fieldType.value.map((field, i) => {
								if(field.editorType && !field.editorType.includes(state.modelType)) {
									return null;
								}
								if(!showField(field)) {
									return null;
								}
								return (
									<div className={classes.fieldContainer} key={i}>
										<Widget
											id={field.id}
											attrs={field}
											design={true}
											component={FieldComponent}
											isCoreField={true}
											isPalleteField={true}
										/>
									</div>
								);
							})}
					</Grid>
				</React.Fragment>
			))}
			{/* {state.metaFields && state.metaFields.length > 0 && (
				<Grid>
					<Typography variant="body1" className={classes.fieldGroupTitle}>
						Existing Fields
					</Typography>
					<ExistingFieldsView />
				</Grid>
			)} */}
		</>
	);
});
