import React from "react";
import convert from "xml-js";
import {
	Dialog,
	DialogTitle,
	DialogContent,
	DialogActions,
	Button,
} from "@material-ui/core";

import Toolbar from "./Toolbar";
import { useStore } from "../store/context";
import {
	generateView,
} from "../store/extensionGenerator";
import AxelorService from "./../services/axelor.rest";
import Utils, { generateCustomModelSchema } from "../utils";
import { MODEL_TYPE, ENTITY_TYPE, conditionProperties, otherNoQuoteProps } from "../constants";
import {
	saveCustomModel,
	fetchJSONFields,
	removeCustomModel,
	generateMenuChange,
	fetchCustomModel,
	fetchCustomFields,
	removeEntityModel,
	fetchViews,
	saveAttrsList,
	fetchAttrsList,
	saveTranslationList,
	getTranslationList,
	deleteAttrsList,
	deleteTranslationList
} from "./api";
import DeleteConfirmation from "./DeleteConfirmation";
import {getXML} from './../store/computeXML';
import {generateXpath, eGeneratePath} from './../store/xpathGenerator';
import {optimizeExtension, getUpdatedAttrsList} from '../store/optimizeExtension';

const metaViewService = new AxelorService({
	model: "com.axelor.meta.db.MetaView",
});
const customFieldService = new AxelorService({
	model: "com.axelor.meta.db.MetaJsonField",
});
const entityModelService = new AxelorService({
	model: "com.axelor.newstudio.db.EntityModel",
});

const getWidgets = ({widgets, items}) => {
	Object.keys(widgets).forEach(widgetId => {
		const path = eGeneratePath(widgets, items, widgetId)
		widgets[widgetId].xPath = path;
	});
	return widgets;
}

const getChangedTranslationList = (list, initialList, removedTranslationList) => {
	const deletedTranslationList = [];
	const changedTranslationList = list.filter(item => {
		if(removedTranslationList.indexOf(item.key) !== -1) {
			item.id && deletedTranslationList.push({id: item.id, version: item.version});
			return false;
		}
		if(item.id) {
			const initialItem = initialList.find(i => i.language === item.language && i.key === item.key);
			if(initialItem && initialItem.message !== item.message) {
				return true;
			}
			return false;
		}
		return true;
	});
	return {changedTranslationList, deletedTranslationList};
}

function ToolbarContainer(props) {
	const { state, update, startLoader, undo, redo, clearHistory } = useStore();
	const [
		deleteConfirmationDialog,
		setDeleteConfirmationDialog,
	] = React.useState(false);

	const closeLoader = React.useCallback(() => {
		update(draft => {
			draft.loader = false;
		})
	}, [update]);

	const removeView = React.useCallback(
		function removeView(message) {
			startLoader();
			const clearView = (dialog) => {
				update((draft) => {
					clearHistory(draft);
					draft.customModel = null;
					draft.dialog = dialog;
					draft.widgets = null;
					draft.initialItems = null;
					draft.initialWidgets = null;
					draft.items = [];
					draft.view = null;
					draft.metaFields = [];
					draft.selectedView = null;
					draft.originalXML = null;
					draft.extensionXML = null;
					draft.selectedView = null;
					draft.loader = false;
					draft.extensionView = null;
					draft.errorList = [];
					if (draft.modelType === MODEL_TYPE.CUSTOM) {
						draft.model = null;
					}
					if(draft.modelType === MODEL_TYPE.ENTITY) {
						draft.entityModel = null;
						draft.model = null;
					}
				});
			};
			if (state.modelType === MODEL_TYPE.CUSTOM) {
				if (state.model || state.customModel) {
					const dialog = {
						message: "View removed successfully",
						type: "alert",
					};
					removeCustomModel(state.model || state.customModel).then((e) => {
						clearView(null);
					});
				}
			} else if (state.modelType === MODEL_TYPE.ENTITY) {
				if(state.entityModel && state.entityModel.id) {
					const dialog = {
						message: "Entity model removed successfully",
						type: "alert",
					};
					removeEntityModel(state.entityModel).then((e) => {
						clearView(null);
					});
				}
			} else {
				let record;
				if (state.extensionView) {
					record = {
						id: state.extensionView.id,
						version: state.extensionView.version,
					};
				} else if(state.selectedView && state.selectedView.xmlId && state.selectedView.xmlId.indexOf("studio-") === 0){
					record = {
						id: state.selectedView.id,
						version: state.selectedView.version,
					};
				}
				if(record) {
					metaViewService.removeAll([record]).then((e) => {
						const dialog = {
							message: message || "View removed successfully",
							type: "alert",
						};
						clearView(null);
					});
				}
			}
		},
		[update, state, startLoader, clearHistory]
	);
	const saveView = React.useCallback(async () => {
		if(!state.widgets) {
			return;
		}
		startLoader();
		if (state.modelType === MODEL_TYPE.CUSTOM) {
			const { fields } = Utils.getCustomModelFields(
				state.items,
				state.widgets,
				null,
				true
			);
			const formProperties = state.widgets[-1];
			if(!formProperties.name || !formProperties.title) {
				const dialog = {
					message: "Please enter form name and title",
					type: "alert",
				};
				update((draft) => {
					draft.loader = false;
					draft.dialog = dialog;
				});
				return;
			}
			let customModel = {
				...state.customModel,
				...Utils.getFormProperties(formProperties),
				fields,
				...(state.customModel && { version: state.customModel.version }),
			};
			if (formProperties.isGenerateMenu === true) {
				customModel["menuBuilder"] = {
					...(state.customModel || {}).menuBuilder,
					title: formProperties.menuBuilderTitle,
					parentMenu: formProperties.menuBuilderParent,
				};
			}
			const { isGenerateMenu } = state.customModel || {};
			if (formProperties.isGenerateMenu !== isGenerateMenu) {
				const res = await generateMenuChange(customModel);
				const { data = [] } = res;
				if (data[0] && data[0].reload) {
					const modelResult = await fetchCustomModel(customModel.id);
					customModel = {
						...customModel,
						menuBuilder: null,
						version: modelResult.version,
					};
				}
			}
			saveCustomModel(customModel).then((res) => {
				const record = res.data[0];
				const { fields = [], ...rest } = record;
				if (rest.menuBuilder) {
					rest.menuBuilder = {
						...customModel.menuBuilder,
						...rest.menuBuilder,
					};
				}
				fetchJSONFields(
					fields.map((f) => f.id),
					update,
					rest
				);
				const dialog = {
					message: "View saved successfully",
					type: "alert",
				};
				update((draft) => {
					draft.customModel = record;
					draft.loader = false;
				});
			});
		} else if (state.modelType === MODEL_TYPE.ENTITY) {
			const formProperties = state.widgets[-1];
			if(!formProperties.name) {
				const dialog = {
					message: "Please enter entity name",
					type: "alert",
				};
				update((draft) => {
					draft.loader = false;
					draft.dialog = dialog;
				});
				return;
			}
			const patch = generateView(state.widgets, state.items, state.modelType);
			const properties = {...formProperties, model: `${formProperties.packageName}.${formProperties.name}`}
			const form = {
				elements: [
					{
						name: "entity",
						type: "element",
						attributes: {
							...Utils.getFormProperties(properties, ["extension", "metaFields", "id", "$attachments", "packageName", "model"]),
						},
						elements: [
							...patch,
						]
					},
				],
			};
			const xml = Utils.processXML(
				convert.json2xml(form, {
					compact: false,
					fullTagEmptyElement: false,
				})
			);
			const data = {
				...(state.entityModel ? state.entityModel : {}),
				entityXml: xml,
				name: formProperties.name,
				packageName: formProperties.packageName,
			};
			entityModelService.save(data).then((res) => {
				const record = res.data[0];
				if (res.status === -1) {
					const dialog = {
						message: res.data.string,
						type: "alert",
					};
					update((draft) => {
						draft.loader = false;
						draft.dialog = dialog;
					});
				} else if (record) {
					const dialog = {
						message: "Model saved successfully",
						type: "alert",
					};
					update((draft) => {
						draft.entityModel = record;
						draft.loader = false;
					});
				}
			});
		} else {
			if (state.widgets) {
				const exemptAttributes = [
					"viewId",
					"modelId",
					"version",
					"helpOverride",
					"menubar",
					"xml",
					"computed",
					"xmlId",
					"type",
					"cols",
					"colSpan",
					"serverType"
				];
				if (!state.exView || Utils.isCreatedFromStudio(state.exView)) {
					const formProperties = state.widgets[-1];
					if(!formProperties.name || !formProperties.title) {
						const dialog = {
							message: "Please enter form name and title",
							type: "alert",
						};
						update((draft) => {
							draft.loader = false;
							draft.dialog = dialog;
						});
						return;
					}
					const viewSearch = await metaViewService.search({data: {_domain: `self.name = '${formProperties.name}'`}});
					if(viewSearch && viewSearch.total && viewSearch.total > 0 && !state.exView?.id) {
						const dialog = {
							message: "View name already exists",
							type: "alert",
						};
						update((draft) => {
							draft.loader = false;
							draft.dialog = dialog;
						});
						return;
					}
					const patch = generateView(
						state.widgets,
						state.items,
						state.modelType
					);
					const model = state.entityType === ENTITY_TYPE.ENTITY ? Utils.getEntityModel(state.entityModel): state.model.fullName;
					const form = {
						elements: [
							{
								name: "form",
								type: "element",
								attributes: {
									model,
									...Utils.getFormProperties(formProperties, [...exemptAttributes, 'extension', 'id']),
									id: state.exView && state.exView.xmlId
											? state.exView.xmlId
											: `studio-${formProperties.name}`,
								},
								elements: [...patch],
							},
						],
					};
					const xml = Utils.processXML(
						convert.json2xml(form, {
							compact: false,
							fullTagEmptyElement: false,
						})
					);
					let data = {
						xml,
						extension: formProperties.extension,
						type: "form",
						name: formProperties.name,
						title: formProperties.title,
						model,
						xmlId: `studio-${formProperties.name}`,
					};
					if (state.exView && state.exView.id) {
						data["id"] = state.exView.id;
						data["version"] = state.exView.version;
						data["xmlId"] = state.exView.xmlId || data.xmlId;
					}
					metaViewService.save(data).then(async (res) => {
						if (res.status === -1) {
							const error = res.data;
							const dialog = {
								title: error.title,
								message: error.string || error.message,
								type: "alert",
							};
							update((draft) => {
								draft.loader = false;
								draft.dialog = dialog;
							});
						} else if (res.data && res.data[0]) {
							const fetchResult = await metaViewService.fetch(res.data[0].id);
							if(fetchResult.data && fetchResult.data[0]) {
								const record = fetchResult.data[0];
								const dialog = {
									message: "View saved successfully",
									type: "alert",
								};
								update((draft) => {
									const schema = Utils.generateXMLToViewSchema({view: record, fields: draft.metaFieldStore});
									draft.widgets = schema.widgets;
									draft.initialWidgets = schema.widgets;
									draft.items = schema.items;
									draft.initialItems = schema.items;
									draft.metaView = record;
									draft.exView = record;
									draft.selectedView = {...record};
									draft.loader = false;
								});
							}
						}
					});
				} else {
						const { view } = state.view;
						const model = state.entityType === ENTITY_TYPE.ENTITY ? Utils.getEntityModel(state.entityModel): state.model.fullName;
						const originalSchema = Utils.generateViewFromJson({view: state.originalXML, fields: state.metaFieldStore});
						const widgets = getWidgets(originalSchema);
						const moves = optimizeExtension(state, widgets);
						const {changedAttrsList, removedAttrsList} = getUpdatedAttrsList(state, widgets);
						const extension = getXML(moves, state);
						let isAttrsChanged = false;
						let attrsList = [];
						if(changedAttrsList.length) {
							isAttrsChanged = true;
							const conditionalValues = Object.values(conditionProperties);
							const list = changedAttrsList.map(attr => {
								if([...conditionalValues, ...otherNoQuoteProps].includes(attr.name)) {
									return attr;
								}
								return {...attr, value: `"${attr.value}"`}
							});
							await saveAttrsList(list);
						}
						if(removedAttrsList.length) {
							isAttrsChanged = true;
							await deleteAttrsList(removedAttrsList);
						}
						if(isAttrsChanged) {
							attrsList = await fetchAttrsList({model: state.model.fullName, view: state.selectedView.name});
						}
						const {changedTranslationList, deletedTranslationList} = getChangedTranslationList(state.translationList, state.initialTranslationList, state.removedTranslationList);
						let translationList = [];
						let isTranslationChanged = false;
						if(changedTranslationList.length) {
							isTranslationChanged = true;
							await saveTranslationList(changedTranslationList);
						}
						if(deletedTranslationList.length) {
							isTranslationChanged = true;
							await deleteTranslationList(deletedTranslationList);
						}
						if(isTranslationChanged) {
							const translationNames = Object.values(state.widgets).filter(widget => widget.title && widget.title.startsWith("studio:")).map(widget => widget.title);
							translationList = await getTranslationList(translationNames);
							update(draft => {
								draft.translationList = translationList;
								draft.initialTranslationList = translationList;
								draft.removedTranslationList = [];
							});
						}
						if(!extension.length && !state.extensionXML) {
							if(state.extensionView) {
								removeView("Removed extension view due to no changes available")
							} else {
								const dialog = {
									message: "View saved successfully",
									type: "alert",
								};
								update(draft => {
									draft.loader = false;
									const views = draft.view;
									if(isAttrsChanged) {
										const schema = Utils.generateXMLToViewSchema({view: views.view, fields: draft.metaFieldStore, extensionXML: views.extensionXML, attrsList});
										draft.widgets = schema.widgets;
										draft.initialWidgets = schema.widgets;
										draft.items = schema.items;
										draft.initialItems = schema.items;
										draft.extensionMoves = [];
										draft.attrsList = attrsList;
										generateXpath(draft);
									}
								});
							}
							return;
						}
						const getExistingExtension = (json) => {
							if(!json) {
								return [];
							}
							const {elements} = json;
							if(elements[0] && elements[0].elements) {
								return elements[0].elements;
							}
						};
						const form = {
							elements: [
								{
									type: "element",
									name: state.widgets[-1].type,
									attributes: {
										model,
										id: `studio-${view.name}`,
										title: state.widgets[-1].title,
										name: view.name,
										extension: 'true'
									},
									elements: [
										...getExistingExtension(state.extensionXML),
										...extension
									],
								},
							],
						};
						const xml = Utils.processXML(
							convert.json2xml(form, {
								compact: false,
								fullTagEmptyElement: false,
							})
						);
						let data = {
							xml,
							extension: true,
							type: state.widgets[-1].type,
						};
						if (state.extensionView) {
							data = {
								...data,
								id: state.extensionView.id,
								version: state.extensionView.version,
							};
						} else {
							data = {
								...data,
								model,
								name: view.name,
								title: view.title,
								xmlId: `studio-${view.name}`,
							};
						}
						console.log(xml);
						if(state.extensionView) {
							if(state.extensionView.xml === data.xml) {
								update(draft => {
									draft.loader = false;
									if(isAttrsChanged) {
										const dialog = {
											message: "View saved successfully",
											type: "alert",
										};
										update(draft => {
											draft.loader = false;
											const views = draft.view;
											const schema = Utils.generateXMLToViewSchema({view: views.view, fields: draft.metaFieldStore, extensionXML: views.extensionXML, attrsList});
											draft.widgets = schema.widgets;
											draft.initialWidgets = schema.widgets;
											draft.items = schema.items;
											draft.initialItems = schema.items;
											if(isAttrsChanged) {
												draft.attrsList = attrsList;
											}
											generateXpath(draft);
										});
									}
								});
								return;
							}
						}
						metaViewService.save(data).then(async(res) => {
							if (res.status === -1) {
								const dialog = {
									message: res.data.string,
									type: "alert",
								};
								update((draft) => {
									draft.loader = false;
									draft.dialog = dialog;
								});
							} else if (res.data && res.data[0]) {
								const record = res.data[0];
								const { view } = state.view;
								const dialog = {
									message: "View saved successfully",
									type: "alert",
								};
								const views = await fetchViews(view);
								update((draft) => {
									if(draft.entityType === ENTITY_TYPE.ENTITY) {
									}
									const schema = Utils.generateXMLToViewSchema({view: views.view, fields: draft.metaFieldStore, extensionXML: views.extensionXML, attrsList});
									draft.widgets = schema.widgets;
									draft.initialWidgets = schema.widgets;
									draft.items = schema.items;
									draft.initialItems = schema.items;
									draft.exView = res.data[0];
									draft.extensionMoves = [];
									draft.loader = false;
									if(isAttrsChanged) {
										draft.attrsList = attrsList;
									}
									draft.extensionView = {
										...draft.extensionView,
										...record,
										version: views.extensionXML.id === record.id ? views.extensionXML.version : record.version,
									};
									generateXpath(draft);
								});
							}
						});
				}
			}
		}
	}, [state, update, startLoader, closeLoader, removeView]);

	const handleClose = React.useCallback(() => {
		update((draft) => {
			draft.dialog = null;
		});
	}, [update]);

	const handleNew = React.useCallback(() => {
		startLoader();
		update((draft) => {
			clearHistory(draft)
			draft.items = [];
			draft.customModel = null;
			draft.exView = null;
			draft.view = null;
			draft.extensionXML = null;
			draft.extensionView = null;
			draft.loader = false;
			draft.metaFields = draft.metaFieldStore;
			draft.selectedView = null;
			draft.translationList = [];
			draft.initialTranslationList = [];
			draft.attrsList = [];
			if(!(draft.modelType === MODEL_TYPE.BASE && draft.entityType === ENTITY_TYPE.ENTITY)) {
				draft.entityModel = null;
			}
			if(draft.modelType === MODEL_TYPE.ENTITY) {
				draft.entityType = ENTITY_TYPE.ENTITY;
				draft.actualType = null;
				draft.model = null;
				draft.metaFields = [];
				draft.metaFieldStore = [];
			}
			if (draft.modelType === MODEL_TYPE.CUSTOM) {
				draft.model = null;
			}
			draft.widgets = {
				"-1": {
					type: "form",
					cols: 12,
					extension: false,
					...Utils.getDefaultProperties(state.modelType, state.entityType, state.entityModel),
				},
				"0": { type: "dump_field", name: "" },
			};
			draft.editWidget = -1;
			draft.editWidgetType = null;
			draft.tabIndex = 0;
		});
	}, [update, state.modelType, startLoader, state.entityType, state.entityModel, clearHistory]);

	const saveCustomFieldView = React.useCallback(() => {
		if ((state.customFieldItems || state.removedCustomFields.length) && state.modelField?.name) {
			startLoader();
			const extraProps = {
				modelField: state.modelField.name,
				model: state.model.fullName,
			};
			const { fields } = Utils.getCustomModelFields(
				state.customFieldItems,
				state.customFieldWidgets,
				extraProps,
				true
			);
			if (fields.length || state.removedCustomFields.length) {
				const all = fields.map((field) => customFieldService.save(field));
				if (state.removedCustomFields.length) {
					all.push(customFieldService.removeAll(state.removedCustomFields));
				}
				Promise.all(all).then((res) => {
					fetchCustomFields(state.modelField, state.model).then((res) => {
						const { data = [] } = res;
						const schema = generateCustomModelSchema(data, undefined, '', 'customForm');
						update((draft) => {
							draft.customFieldWidgets = schema.widgets;
							draft.customFieldItems = schema.items;
							draft.customFields = data;
							draft.loader = false;
							draft.removedCustomFields = [];
						});
					});
				});
			} else {
				closeLoader();
			}
		}
	}, [update, closeLoader, state, startLoader]);

	const handleConfirmationClose = React.useCallback(() => {
		setDeleteConfirmationDialog(false);
	}, []);

	const deleteView = React.useCallback(() => {
		setDeleteConfirmationDialog(true);
	}, []);

	const handleRemoveConfirmation = React.useCallback(() => {
		handleConfirmationClose();
		removeView();
	}, [removeView, handleConfirmationClose]);

	const handleSidePanelToggle = React.useCallback(() => {
		update(draft => {
			draft.sidePanel = !draft.sidePanel;
		});
	}, [update]);

	function renderDialog() {
		const { dialog } = state;
		return (
			dialog && (
				<Dialog open={Boolean(dialog.message)}>
					<DialogTitle>{dialog.title || 'Alert'}</DialogTitle>
					<DialogContent>
						<div dangerouslySetInnerHTML={{__html: dialog.message}}>

						</div>
					</DialogContent>
					<DialogActions>
						<Button onClick={handleClose} color="primary" autoFocus>
							OK
						</Button>
					</DialogActions>
				</Dialog>
			)
		);
	}

	return (
		<React.Fragment>
			{renderDialog()}
			<Toolbar
				update={update}
				saveView={saveView}
				saveCustomFieldView={saveCustomFieldView}
				removeView={deleteView}
				model={state.model}
				extensionView={state.extensionView}
				modelType={state.modelType}
				onNew={handleNew}
				modelField={state.modelField}
				customModel={state.customModel}
				selectedView={state.selectedView}
				startLoader={startLoader}
				entityModel={state.entityModel}
				errorList={state.errorList}
				entityType={state.entityType}
				metaFieldStore={state.metaFieldStore}
				undo={undo}
				redo={redo}
				pastLength={state.past.length}
				futureLength={state.future.length}
				clearHistory={clearHistory}
				onSidePanelToggle={handleSidePanelToggle}
			/>
			<DeleteConfirmation
				open={deleteConfirmationDialog}
				onClose={handleConfirmationClose}
				onOk={handleRemoveConfirmation}
				message="Are you sure? Do you want to delete it?"
			/>
		</React.Fragment>
	);
}

export default React.memo(ToolbarContainer);
